<template>
  <router-view v-if="loaded"/> <!-- 把路由对应过来，router/index.js中定义的当前的页面 -->
</template>

<script>
import * as d3 from 'd3';
import store from './store/index.js';

export default {
  name: 'App',
  components: {
  },
  data () {
    return {
      loaded: false,
    }
  },
  computed: {
  },
  methods: {
  },
  mounted() {
    d3.json('./data.json').then(data => {
      // 数据预处理
      data = data.map(d => {
        return {
          time: new Date(d["日期"].slice(0, 4), d["日期"].slice(4, 6) - 1, d["日期"].slice(6, 8)),
          // newsCount: d["新闻"].length,
          newsCount: d["新闻总数"],
          imgCount: d["图片总数"],
          imgColor: d["颜色"],
          news: d["新闻"],
          inkratio: d["墨水比"],
        }
      })
      store.state.data = data;
      this.loaded = true;
    });
  },
}
</script>

